import React, { useState, useEffect } from "react";
import BasicNavBar from "./basicNavBar";
import Lottie from "lottie-react";
import sakuraAnimation from "../../../lottie/cherry-flowers.json";
import { Text } from '@chakra-ui/react';
import cn from 'classnames';
import useMobileDetect from 'use-mobile-detect-hook';

import "@fontsource/dancing-script";
import "./styles.scss";

const lottieOptions = {
    animationData: sakuraAnimation,
    loop: true,
    autoplay: true,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
    }
};

const mobileLottieContainerHeight = 200,
    desktopLottieContainerHeight = 300,
    desktopFontSizeMax = 100,
    desktopFontSizeMin = 60,
    mobileFontSizeMax = 60,
    mobileFontSizeMin = 30;

const CollapasingNavBar = ({ curRoute, htmlAst }) => {
    const detectMobile = useMobileDetect();
    const [isShrunkState, setIsShrunk] = useState({
        isShrunk: false,
        updated: new Date(),
    });

    useEffect(() => {
        const handler = () => {
            setIsShrunk((curIsShrunk) => {
                const offset = window.pageYOffset;
                const { prevYOffset } = curIsShrunk;

                const goingDown = offset > prevYOffset;

                const lottieHeight = (detectMobile ? mobileLottieContainerHeight : desktopLottieContainerHeight);
                
                let shouldShrink = false;
                if (goingDown) {
                    shouldShrink = offset > (lottieHeight + 24);
                } else {
                    shouldShrink = offset > lottieHeight;
                }

                return {
                    isShrunk: shouldShrink,
                    prevYOffset: offset,
                };
            });
        };

        window.addEventListener("scroll", handler);
        return () => window.removeEventListener("scroll", handler);
    }, [detectMobile]);

    const { isShrunk } = isShrunkState;
    let fontSize = desktopFontSizeMax;
    if (detectMobile) {
        fontSize = isShrunk ? mobileFontSizeMin : mobileFontSizeMax;
    } else {
        fontSize = isShrunk ? desktopFontSizeMin : desktopFontSizeMax;
    }

    return (
        <div className={cn("Navigation", { "IsShrunk": isShrunk })}>
            <div className="NavigationHeader">

                <Text className="AAndM" style={{ fontSize: `${fontSize}px` }}>A + M</Text>
            </div>

            <BasicNavBar hamburgerIconColor={isShrunk ? 'white.800' : 'pink.400'} curRoute={curRoute} htmlAst={htmlAst} />
        </div>
    );
};

export const NavBar = ({ curRoute, htmlAst }) => {
    const detectMobile = useMobileDetect();

    return (
        <>
            <Lottie
                className="LottieAnimation"
                {...lottieOptions}
                style={{
                    height: detectMobile ? mobileLottieContainerHeight : desktopLottieContainerHeight,
                    width: '100%'
                }}
            />
            <CollapasingNavBar curRoute={curRoute} htmlAst={htmlAst} />
        </>
    );
};

export default NavBar;