import renderAst from "./renderAst";
import React from "react";
import Menu from "./menu";
import PageFooter from "./components/Footer";
import { ChakraProvider } from '@chakra-ui/react';

import { Heading } from '@chakra-ui/react';
import { Box } from "@chakra-ui/react";

import useMobileDetect from 'use-mobile-detect-hook';
import "./styles.scss";

const Stream = (props) => {
  const detectMobile = useMobileDetect();
  const {childMarkdownRemark, menu, name, showTitle} = props.data.page;
  const {htmlAst} = childMarkdownRemark;

  return (
    <ChakraProvider>
      <Menu route={menu} curRoute={props.location.pathname} />

      <main className="MainSection">
        <Box p={detectMobile ? 4 : 8} maxW="800px" mx="auto">
          {showTitle && name && <Heading className="Header" as="h1">{name}</Heading>}

          {renderAst(htmlAst)}
        </Box>
      </main>
      <PageFooter />
    </ChakraProvider>
  );
}

export default Stream 